   </div>

    <div v-if="connectedContract == null">
      <button v-on:click="connectContract" class="contract-button">컨트랙트 연결</button>
    </div>
    <div v-else >
      <!-- ownerof 함수 호출 -->
      <div class="container">
        <div class="title">ownerOf</div>
        <div class="result-container">
          <span>tokenId</span>
          <input class="token-input" type="text" placeholder="tokenId" v-model="ownerOf_tokenId"/>
          <br/>
          <button v-on:click="ownerOf" class="call-button">call</button>
        </div>
