        <span>ownerof:</span>
        <span>{{ownerOf_result}}</span>
      </div>
      </div>
      </div>
      <div class="container">
        <!-- blanceof -->
        <div className="title">blanceOf</div>
        <div class="result-container">
          <span>balanceOf</span>
          <input class="token-input" type="text" placeholder="balanceOf" v-model="balanceOf_count"/>
          <br/>
          <button v-on:click="balanceOf" class="call-button">call</button>
        </div>
      </div>
      <div class="container">
        <div className="title">Mint</div>
        <div class="result-container-last">
          <div class="wrapper">
          <span>address</span>
          <input class="token-input" type="text" placeholder="address" v-model="mint_to"/>
        </div>
        <div class="wrapper">
        <span>tokenId</span>
        
        <input class="token-input" type="text" placeholder="tokenId" v-model="mint_tokenId"/>
      </div>
