        <span>uri</span>
        <input class="token-input" type="text" placeholder="uri" v-model="mint_uri"/>
      </div>

          </div>

          <div class="mint-button"><button v-on:click="mint" class="call-button">mint</button></div>
          <div v-if="mint_result != null" class="result-result">
          {{mint_result}}
          </div>
      </div>
    </div>
    </div>


