<script>
import {ethers} from "ethers";
import abi from "../assets/abi.json";
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return{
      isInstallMataMask: window.ethereum && window.ethereum.isMetaMask == true,
      currentAddress : null,
      connectedContract:null,
      ownerOf_tokenId :null,
      ownerOf_result: null,
      balanceOf_count: null,
      mint_to : null,
      mint_tokenId : null,
      mint_uri : null,
      mint_result:null

    }
  },
  methods:{
    async connectMetaMask() 
    {
      console.log('connectmeta 호출')
      var account = await window.ethereum.request({method:"eth_requestAccounts"});
      if(account.length >0) {
        this.currentAddress = account[0];
        console.log(this.currentAddress)
      }
      else {
        console.log("계정이 업네")
      }
    },
    async connectContract() 
    {
      console.log('connectContract 호출');
      var contractAdress = "0xB2e63330ce77dea8cDa14AB3BC92A39FF181a8E6";
      var provider = new ethers.providers.Web3Provider(window.ethereum);

      var tempSigner =  provider.getSigner();
      var connectedContract =  new ethers.Contract( contractAdress,abi, tempSigner );
      console.log(connectedContract);
      this.connectedContract = connectedContract;
    },
    async ownerOf()
    {
      console.log('ownerOf 호출');

      var ownerAddress = await this.connectedContract.ownerOf(this.ownerOf_tokenId);
      console.log(ownerAddress)
      this.ownerOf_result = ownerAddress;
    },
    async balanceOf()
    {
      console.log('dd')
      this.balanceOf_count = await this.connectedContract.balanceOf(this.currentAddress);

    },
    async mint() {
      try{
        console.log('mint');
        var tranaction = await this.connectedContract.mint(this.mint_to, this.mint_tokenId, this.mint_uri);
        console.log(tranaction);
        this.mint_result = "생성 대기중";
        await tranaction.wait();
        this.mint_result =  tranaction;
        console.log(this.mint_result)
      }
      catch(err){
        console.log(err);
        this.mint_result = err;
      }
      
    }
  }
}
</script>

